import { Link } from 'react-router-dom';
import Login from './Login'

const User = ({userProps}) => {

    let user = userProps.user;
    let toLogout = userProps.toLogout;
    let toLogin = userProps.toLogin;


    let display;

    if (user) {
        display = (
            <div>
                <h1>Welcome back {user.username}</h1>
                <button onClick={toLogout}>logout</button>
                <Link to="/mybids">see bids</Link>
            </div>
        )
    } else {
        display = <Login toLogin={toLogin}/>
    }

    return (
        display
    )
}

export default User
