import { useState } from 'react'
import Countdown from './Countdown'
import Artwork from '../gallery/Artwork'

const Auction = ({match, auctionProps}) => {

    const initState = {bid: "", auctionId: ""}
    const [formState, setFormState] = useState(initState)

    let auction;
    let artwork = null;
    let time;
    let toBid = auctionProps.toBid

    if (!auctionProps.auctions) {
        auctionProps.call()
    } else if (auctionProps.artworks && auctionProps.auctions) {
        artwork = auctionProps.artworks.filter(artwork => artwork.name === match.artwork)[0]
        auction = auctionProps.auctions.filter(auction => auction.artwork === artwork._id)[0]
        time = new Date(auction.time.end)
    }
    // const [success, setSuccess] = useState("")

    const handleSubmit = (event) => {
        event.preventDefault();
        if (formState.bid < auction.currentMax + 1) {
            alert("must place a bid greater or ewual to minimum bid value")
        } else {
            setFormState({...formState, auctionId: auction._id})
            toBid(formState)
        }
        setFormState(initState);
    }

    const handleChange = (event) => {
        setFormState({...formState, [event.target.id]: event.target.value})
    }

    let structure = artwork ?
    (
        <div className="auction-container">
            <Artwork artwork={artwork} />
            <h2 className="current-max">{auction.currentMax}</h2>
            <h4 className="auction-ends">Auction Ends</h4>
            <Countdown expiryTimestamp={time} />
            <form onSubmit={handleSubmit} className="bid-form">
                <label className="bid" htmlFor="bid">Bid</label>
                <input
                    id="bid"
                    type="text"
                    onChange={handleChange}
                    value={formState.bid}
                />
                <button type="submit" className="bid-btn">Place Bid</button>
            </form>
            <p className="minimum-bid">minimum bid: {auction.currentMax + 1}$</p>
        </div>

    ) : null;


    return structure
}

export default Auction