import './App.css';
import axios from 'axios';
import React, { useState, useEffect } from 'react';
import { Link, Redirect, Route } from "react-router-dom";
import User from './components/user/User';
import About from './components/about/About';
import Signup from './components/user/Signup';
import Home from './components/Home';
import Super from './components/super/Super';
import AuctionList from './components/auctions/AuctionList';
import MyBids from './components/user/bids/MyBids';
import Auction from './components/auctions/Auction';

function App() {

    const [artworks, setArtworks] = useState(null)
    const [auctions, setAuctions] = useState(null)
    const [myBids, setMyBids] = useState({active: null, all: null, won: null})
    const [user, setUser] = useState(null)

    const getArtworks = () => {
        axios({
            method: "GET",
            withCredentials: true,
            url: "https://name-pending-backend.herokuapp.com/art"
        })
        .then(res => {
            setArtworks(res.data)
        })
    }

    const getAllBids = () => {
        axios({
            method: "GET",
            withCredentials: true,
            url: "https://name-pending-backend.herokuapp.com/bids/all"
        })
        .then(res => {
            setMyBids({...myBids, all: res.data})
        })
    }

    const getActiveBids = () => {
        axios({
            method: "GET",
            withCredentials: true,
            url: "https://name-pending-backend.herokuapp.com/bids/active"
        })
        .then(res => {
            setMyBids({...myBids, active: res.data})
        })
    }

    const getWonBids = () => {
        axios({
            method: "GET",
            withCredentials: true,
            url: "https://name-pending-backend.herokuapp.com/bids/active"
        })
        .then(res => {
            setMyBids({...myBids, won: res.data})
        })
    }

    const getAuctions = () => {
        axios({
            method: "GET",
            withCredentials: true,
            url: "https://name-pending-backend.herokuapp.com/auctions/active"
        })
        .then(res => {
            setAuctions(res.data)
        })
    }

    const toBid = (data) => {
        const payload = data;
        axios({
            method: "POST",
            data: payload,
            withCredentials: true,
            url: "https://name-pending-backend.herokuapp.com/bids"
        })
        .then((res) => {
            console.log(res)
            if (res.status === 200) {
                getAuctions()
            } 
        })
        .catch((err) => {
            window.location.replace("/user")
        })
    }

    const toLogin = (data) => {
        const payload = data;
        axios({
            method: "POST",
            data: payload,
            withCredentials: true,
            url: "https://name-pending-backend.herokuapp.com/user/login"
        })
        .then(() => {
            getUser()
        })
        
    }

    const toLogout = () => {
        axios({
            method: "GET",
            withCredentials: true,
            url: "https://name-pending-backend.herokuapp.com/logout"
        })
        .then(res => {
            setUser(null)
        })
        
    }

    const toSignup = (data) => {
        const payload = data;
        axios({
            method: "POST",
            data: payload,
            withCredentials: true,
            url: "https://name-pending-backend.herokuapp.com/new"
        })
        .then(res => {
            console.log(res.data)
        })

    }

    const getUser = () => {
        axios({
            method: "GET",
            withCredentials: true,
            url: "https://name-pending-backend.herokuapp.com/user/"
        })
        .then(res => {
            console.log(res)
            setUser(res.data);
        })
    }

    const toAuction = (data) => {
        const payload = data;
        axios({
            method: "POST",
            data: payload,
            withCredentials: true,
            url: "https://name-pending-backend.herokuapp.com/auctions"
        })
        .then(res => {
            console.log(res.data)
        })
    }

    const auctionCall = () => {
        getAuctions()
        getArtworks()
    }

    useEffect(() => {
        getUser()
        getArtworks()
        getAuctions()
    }, [])


    const auctionProps = {artworks: artworks, auctions: auctions, call: auctionCall, toBid: toBid}
    const bidProps = {bids: myBids, getActiveBids: getActiveBids, getAllBids: getAllBids, getWonBids: getWonBids}
    const userProps = {user: user, toLogin: toLogin, toSignup: toSignup, getUser: getUser}


    return (
      <div className="App">
          <nav>
            <Link className="nav-item vertical" id="nav-user" to="/user">profile</Link>
            <Link className="nav-item vertical" id="nav-about" to="/about">about</Link>
            {/* <Link className="nav-item" id="nav-admin" to="/super">riley</Link> */}
            <Link className="nav-item vertical" id="nav-auc" to="/auctions">auction</Link>
            <Link className="nav-item vertical" id="nav-art" to="/">gallery</Link>
          </nav>
          <main>
            <Route exact path="/" render={() => <Home artworks={artworks} />} />
            <Route exact path="/art/:artwork" render={(routerProps) => <Auction auctionProps={auctionProps} match={routerProps.match.params} />} />
            <Route exact path="/auctions" render={() => <AuctionList auctionProps={auctionProps}/>} />
            <Route exact path="/about" component={About} />
            <Route exact path="/signup"  render={() => <Signup toSignup={toSignup}/>}/>
            <Route exact path="/mybids"  render={() => <MyBids bidProps={bidProps} />}/>            
            <Route exact path="/user" render={() => <User userProps={userProps} /> } />
            <Route exact path="/super" render={() => <Super toAuction={toAuction} artworks={artworks} />} />
          </main>
      </div>
    );
}

export default App;
