import React, {useState} from 'react'
import { Link } from 'react-router-dom'
import ArtLite from './ArtLite'

const Gallery = ({artworks}) => {

    const data = []

    for (let i = 0; i < artworks.length; i++) {
        let photo = "https://name-pending-backend.herokuapp.com/" + artworks[i].pictures[0];
        let name = artworks[i].name;
        let id = artworks[i]._id
        data.push(
            <Link to={`/art/${name}`} ><ArtLite key={id} artwork={photo}/></Link>
        )
    }

    return (
        <div className="gallery-container central">
            {data}
        </div>
    )
}

export default Gallery
