import React from 'react'
import { Link } from 'react-router-dom';
import Countdown from './Countdown'

const AuctionLite = ({auction, artwork}) => {
    let picture = "https://name-pending-backend.herokuapp.com/" + artwork.pictures[0];
    let enddate = new Date(auction.time.end).toLocaleDateString();
    let endtime = new Date(auction.time.end).toLocaleTimeString('en-US', { hour: '2-digit', minute: '2-digit' });
    if (endtime[0] === '0') {
        endtime = endtime.slice(1);
    }

    return (
        <div className="auction-lite">
            <div className="art-component">
                <img className="artwork-image-lite" src={picture} alt="art"></img>
            </div>
            <div className="auction-component vertical">
                <h3 className="artwork-name">{artwork.name}</h3>
                <h4 className="auction-price">highest bid: {auction.currentMax}$</h4>
                <p className="end-text" >auction. ends.</p>
                <p className="end-date" >{enddate}, {endtime}</p>
                <Link className="bid-now" to={`/art/${artwork.name}`}>bid now</Link>
            </div>
        </div>
    )
}

export default AuctionLite
