import React from 'react'
import AuctionLite from './AuctionLite';

const AuctionList = ({auctionProps}) => {

    const data = []

    let auctions = auctionProps.auctions;
    let artworks = auctionProps.artworks;


    if (!auctions || !artworks) {
        auctionProps.call()
    }

    if (auctions && artworks) {
        for (let i = 0; i < auctions.length; i++) {
            
            let artwork = artworks.filter(item => item._id === auctions[i].artwork)[0];
    
            data.push(
                <AuctionLite key={auctions[i]._id} auction={auctions[i]} artwork={artwork} />
            )
        }
    }

    return (
        <div className="auction-lite-container">
            {data}
        </div>
    )
}

export default AuctionList
